import React, { useCallback } from "react"
import { Watchlist } from "@rp/common/api-types"
import { Radio } from "@rp/react/web/mui/core"

type BriefWatchlistItemRadioButtonProps = {
  watchlist: Watchlist
  disabled?: boolean
  watchlistId: string
  setWatchlistId: (watchlistId: string) => void
  setName: (name: string) => void
}

const BriefWatchlistItemRadioButton = ({
  watchlist,
  disabled,
  watchlistId,
  setWatchlistId,
  setName,
}: BriefWatchlistItemRadioButtonProps) => {
  const isChecked = watchlist.id === watchlistId

  const handleRadioClick = useCallback(() => {
    setWatchlistId(isChecked ? "" : watchlist.id)
    setName(isChecked ? "" : watchlist.name)
  }, [isChecked, setName, setWatchlistId, watchlist])

  return (
    <Radio
      disabled={disabled}
      checked={isChecked}
      onClick={handleRadioClick}
      inputProps={{ "aria-checked": isChecked, "aria-label": watchlist.name }}
    />
  )
}

export { BriefWatchlistItemRadioButton }
